.App-BotResponse {
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100vw;
}

.App-BotResponse-Activity {
  background-color: transparent;
  border-radius: 2vmin;
  color: gray;
  font-size: 6vmin;
  margin: 4vmin 4vmin 0;
  padding: 2vmin 3vmin 2vmin;
}

.App-BotResponse-Attachments {
  list-style-type: none;
  margin: 0;
  zoom: 2;
}

.App-BotResponse-Attachments .App-BotResponse-Attachment {
  background-color: White;
  border-radius: 1vmin;
  margin: 2vmin 0 2vmin 2vmin;
  width: auto;
  max-width: 72vw;
}

.App-BotResponse-Attachments li:last-child {
  padding-right: 2vmin;
}
